/* -------------------- .swing -------------------- */
.swing {
  display: inline-block;
  width: 215px;
  padding: 10px 0 10px 15px;
  font-family: "Open Sans", sans;
  font-weight: 400;
  color: #377d6a;
  /* background: #efefef; */
  border: none;
  border-radius: 3px;
  outline: none;
  text-indent: 60px; /* Arbitrary */
  transition: all 0.3s ease-in-out;
}
.swing::-webkit-input-placeholder {
  color: #efefef;
  text-indent: 0;
  font-weight: 300;
}
.swing + label {
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
  padding: 10px 15px;
  text-shadow: 0 1px 0 rgba(19, 74, 70, 0.4);
  background: #7ab893;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  transform-origin: 2px 2px;
  transform: rotate(0);
  animation: swing-back 0.4s ease-in-out 1;
}

@keyframes swing {
  0% { transform: rotate(0); }
  20% { transform: rotate(116deg); }
  40% { transform: rotate(60deg); }
  60% { transform: rotate(98deg); }
  80% { transform: rotate(76deg); }
  100% { transform: rotate(82deg); }
}

@keyframes swing-back {
  0% { transform: rotate(82deg); }
  100% { transform: rotate(0); }
}

.swing:focus,
.swing:active {
  color: #377d6a;
  text-indent: 0;
  /* background: #fff; */
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.swing:focus::-webkit-input-placeholder,
.swing:active::-webkit-input-placeholder {
  color: #aaa;
}
.swing:focus + label,
.swing:active + label {
  animation: swing 1.4s ease-in-out 1;
  transform: rotate(82deg);
}

/* -------------------- .balloon -------------------- */
.balloon {
  display: inline-block;
  width: 215px;
  padding: 10px 0 10px 15px;
  font-family: "Open Sans", sans;
  font-weight: 400;
  color: #377d6a;
  /* background: #efefef; */
  border: none;
  border-radius: 3px;
  outline: none;
  text-indent: 60px; /* Arbitrary */
  transition: all 0.3s ease-in-out;
}
.balloon::-webkit-input-placeholder {
  color: #efefef;
  text-indent: 0;
  font-weight: 300;
}
.balloon + label {
  display: inline-block;
  position: absolute;
  top: 8px;
  left: 0;
  bottom: 8px;
  padding: 5px 15px;
  color: #032429;
  font-size: 11px;
  font-weight: 700;
  text-transform: uppercase;
  text-shadow: 0 1px 0 rgba(19, 74, 70, 0);
  transition: all 0.3s ease-in-out;
  border-radius: 3px;
  background: rgba(122, 184, 147, 0);
}
.balloon + label:after {
  position: absolute;
  content: "";
  width: 0;
  height: 0;
  top: 100%;
  left: 50%;
  margin-left: -3px;
  border-left: 3px solid transparent;
  border-right: 3px solid transparent;
  border-top: 3px solid rgba(122, 184, 147, 0);
  transition: all 0.3s ease-in-out;
}
.balloon:focus,
.balloon:active {
  color: #377d6a;
  text-indent: 0;
  /* background: #fff; */
}
.balloon:focus::-webkit-input-placeholder,
.balloon:active::-webkit-input-placeholder {
  color: #aaa;
}
.balloon:focus + label,
.balloon:active + label {
  color: #fff;
  text-shadow: 0 1px 0 rgba(19, 74, 70, 0.4);
  background: rgba(122, 184, 147, 1);
  transform: translateY(-40px);
}
.balloon:focus + label:after,
.balloon:active + label:after {
  border-top: 4px solid rgba(122, 184, 147, 1);
}










/* Container styling */
.bg-black {
  background-color: #000;
}
.w-full {
  width: 100%;
}
.min-h-screen {
  min-height: 100vh;
}
.flex {
  display: flex;
}
.flex-col {
  flex-direction: column;
}
.items-center {
  align-items: center;
}
.justify-center {
  justify-content: center;
}
.relative {
  position: relative;
}
.mt-4 {
  margin-top: 1rem;
}

/* Image container & overlay */
.img-container {
  position: relative;
}

.img-container img {
  max-width: 100%;
  height: auto;
  display: block;
}

.winner-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  animation: fadeIn 1s ease-in-out;
}

.winner-text {
  color: gold;
  font-size: 2rem;
  font-weight: bold;
  animation: popUp 1s ease;
}

/* Input and label styling (minimal version) */
.row {
  text-align: center;
}


/*  */



.row span label {
  position: absolute;
  top: 0;
  left: 0;
  padding: 10px 15px;
  background: #7ab893;
  color: #fff;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  transition: all 0.3s ease;
}

/* Animation keyframes */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes popUp {
  from { transform: scale(0.5); }
  to { transform: scale(1); }
}

/* Message styling */
.message p {
  font-family: "Open Sans", sans-serif;
  font-size: 1.25rem;
  margin: 0;
}
